import React from "react";
import { Component } from "react";
import { motion } from "framer-motion";
import RingLoader from "react-spinners/RingLoader";

class Navbar extends Component{
    constructor(props){
        super(props)
        this.state = {
            status : false
        }
    }

    componentDidMount(){
        setTimeout(() => this.setState({status:true}),1500)
    }

    render(){
        return(
            <>
            {
                this.state.status ? 
                <motion.nav transition={{type:'spring',stiffness:200}} className="text-center py-2 cursor-pointer text-2xl text-gray-700">
                <motion.i whileHover={{scale:1.3}} transition={{type:"spring",stiffness : 600}} className='bx bxl-facebook p-2 hover:text-blue-700' />
                <motion.i whileHover={{scale:1.3}} transition={{type:"spring",stiffness : 600}} className='bx bxl-whatsapp p-2 hover:text-green-400' />
                <motion.i whileHover={{scale:1.3}} transition={{type:"spring",stiffness : 600}} className='bx bxl-linkedin-square p-2 hover:text-blue-800' />
                <motion.i whileHover={{scale:1.3}} transition={{type:"spring",stiffness : 600}} className='bx bxl-telegram p-2 hover:text-blue-400' />
                <motion.i whileHover={{scale:1.3}} transition={{type:"spring",stiffness : 600}} className='bx bxl-instagram-alt p-2 hover:text-red-600' />
                </motion.nav>
                :
                <div className="flex justify-center">
                    <div className="m-3 animate-pulse bg-gray-400 rounded-full w-6 h-6" />
                    <div className="m-3 animate-pulse bg-gray-400 rounded-full w-6 h-6" />
                    <div className="m-3 animate-pulse bg-gray-400 rounded-full w-6 h-6" />
                    <div className="m-3 animate-pulse bg-gray-400 rounded-full w-6 h-6" />
                    <div className="m-3 animate-pulse bg-gray-400 rounded-full w-6 h-6" />
                </div>
            }
            
            <div className="flex justify-center mt-7">
                <RingLoader color={"#B45309"}/>
            </div>
            </>
        );
    }
}

export default Navbar;