import React,{Component} from "react";
import Typewriter from 'typewriter-effect';

class Container extends Component{
    render(){
        return(
            <div className="text-center mt-14">
                <h1 className="font-sacramento text-3xl md:text-5xl font-bold text-gray-700">Hello <span className="text-yellow-700">Saya</span></h1>
                <h1 className="font-Montserrat text-5xl p-5 md:text-7xl font-bold text-gray-700 mt-4"><span className="text-yellow-700">Ar</span>is <span className="text-yellow-700">Mun</span>andar</h1>
                <span className=" text-xl md:text-2xl font-Montserrat font-bold text-gray-700 tracking-widest p-5">
                    <Typewriter options={{
                        strings: [`<span>Frontend Developer</span>`, `<span>Backend Developer</span>`, `<span>Frontend Developer</span>`],
                        autoStart: true,
                        loop: true,
                    }}/>
                </span>

            </div>
        );
    }
}

export default Container;